*{
  margin:0;
  padding:0;
}
body{
  min-height: 100vh;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background-color: var(--primary--color);

  
}
/* -------Scroll Effect------------ */
html{
  scroll-behavior: smooth;
}




/* ------------------------------Navbar CSS---------------------------------------- */
nav ul{
  width:100%;
  list-style: none;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  background-color:var(--primary--color);

}
nav li {
  height: 50px;
}
 nav a{
  height : 100%;
  padding : 0 26px;
  text-decoration: none;
  display: flex;
  align-items: center;
  color :black;
 }
 nav a:hover{
  background-color: rgb(206, 15, 15);
  color: #fff;
  border-radius: 10px;
  text-decoration: underline;
 }

 nav li:first-child{
  margin-right: auto;
 }

 .sidebar{
  position:fixed;
  top: 70px;;
  right:0;
  height:100vh;
  width:250px;
  z-index :999;
  background-color: rgba(223, 24, 24, 0.3);
  backdrop-filter:blur(20px);
  box-shadow: -10px 0 10px rgba(0,0,0,0.1);
  display: none;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
 }
 .menu-button{
  display: none;
 }
 .sidebar li{
  width : 100%;
 }
 .sidebar a{
  width:100%;
 }
 @media(max-width:800px){
  .hideOnMobile{
    display: none;
  }
  .menu-button{
    display : block;
  }
 }
 @media(max-width:400px){
  .sidebar{
    width:100%;
  }
 }
 .saran{
  font-size: 33px;
  font-weight: 720;
  color :rgb(6, 6, 6);
  display: inline-block;
  margin-top:2px;
  transition:1s;
  height : 100%;
  padding : 0 26px;
  text-decoration: none;

}

.saran:hover{
transform :rotate(20deg);
cursor: pointer; 
background-color: #ebf0f7;
text-decoration: none;

}
.ran{
  color:rgb(206, 15, 15);
}
.hideOnMobile{
  font-size: 17px;
  font-weight:650;
 
  margin-top:2px;
  padding:2px;
}
.home{
  color:rgb(206, 15, 15);
  font-weight:650;
  text-decoration: underline;
} 

.home:hover{
  color:#fff
}
.navbar{
  z-index:1000;
  position: fixed;
 
}
#icon{
  width:30px;
  cursor:pointer;
  margin-top:10px;
  margin-right:15px;
}
:root{
  --primary--color:#ebf0f7;
  --secondary--color:#0b0b0b; 
  --ternary--color:#fff;
}
.dark-theme{
   --primary--color:black;
  --secondary--color:white; 
  --ternary--color:black;
}
.hideOnMobile a{
  color:var(--secondary--color);
}


/* -------------------------------Home CSS----------------------------------- */
.homeintro{
  padding-top: 80px;
}
.intro{
margin-top: 80px;
margin-left: 120px;
}
.introImg{
  margin-top: 40px;
  transition:.5s;
  
 
}
.introImg:hover{
    transform: scale(1.2);
    cursor: pointer;
    box-shadow: 0 0 15px rgb(241, 0, 0);
}

