  transform: rotate(360deg);
  cursor:pointer;
  box-shadow: 0 0 15px rgb(206, 15, 15);
}
.ghi4{
  position: relative;
  margin-top: 2px;
  height:50px;
  transition: .5s;
  margin-left: -100px;
}
.ghi4:hover{
  transform: rotate(360deg);
  cursor:pointer;
  box-shadow: 0 0 15px rgb(206, 15, 15);}

.ghi5{
  position: relative;
 margin-top: 2px; 
  width : 45px;
  height:45px;
  transition: .5s;
  margin-left: -100px;
}
.ghi5:hover{
  transform: rotate(360deg);
  cursor:pointer;
  box-shadow: 0 0 15px rgb(206, 15, 15);
}

.intro h1{

  color:var(--secondary--color);
}
