/* ----------------About CSS---------------- */
.ref{
  background-color: var(--ternary--color);
  padding-top: 50px;
}

.rt{
  color : rgb(206, 15, 15);
  font-weight: 750;
  background-color:var(--ternary--color);

}

.image{
  width : 400px;
  height : 500px;
  border-radius:50px 100px 10px ;
  /* border-radius: 50%; */
  /* border : 2px solid rgb(206, 15, 15); */
  margin-left:40px;
  margin-bottom:100px;
  /* box-shadow: 0 0 15px rgb(206, 15, 15); */
  float: left;

  
}
.image {
  -webkit-animation: mover 2s infinite  alternate;
  animation: mover 2s infinite  alternate;
}
@-webkit-keyframes mover {
  0% { transform: translateY(0); }
  100% { transform: translateY(-20px); }
}
@keyframes mover {
  0% { transform: translateY(0); }
  100% { transform: translateY(-20px); }
}
.ref h1{
  color:var(--secondary--color)
}
.ref li{
  color:var(--secondary--color)
}

/* -----------------Project CSS----------- */
.click{
  text-align:center;
  font-family: inherit;
  font-weight: bold;
  color:var(--ternary--color);
  border: none;
}
.card-box{
  border-radius: 10px;
  margin-right: 30px;
  /* background-color: #ffffff; */
  box-shadow : 2px 4px 12px rgba(0, 0, 0, 0.3);
  border-radius: 20px;
  border:5px solid rgb(255, 255, 255);
  transition : transform .5s;

}
.card-box:hover{
  transform: scale(.9);
  cursor: pointer;
  box-shadow: 0 0 15px rgb(241, 0, 0);
}
.card-box img{
  border-radius: 15px;
}
.card-body-box p{
  margin-top: 10px;
  font-size: 16px;
  margin-right: 10px;
}
/* -----------------Skills CSS----------------- */
.skills {
  background-color: var(--ternary--color);
  padding-top: 50px;
  padding-bottom: 100px;
 
 }
 .imagee1{
  /* margin-right: 50px; */
  margin-left: 20px;
  border: 2px solid #e4e9f0;
  border-radius: 10px;
  transition: 1s;
  padding-top: 20px;
  margin-bottom: 20px;
 }
 .imagee1 img{
  height:160px;
  width:150px;
  padding-left: 20px;;
 }
 .imagee1:hover{
  box-shadow:0 0 15px rgb(206, 15, 15); 
  cursor: pointer;
  scale: 1.2;
 }

 /* ------------------------Contact CSS--------------------- */
.contact{
  padding-top: 80px;

}
 .ti{
  color:rgb(206, 15, 15);
}
.contact1{
  height:500px;
  width:350px;
  margin-left: 100px;
  transition: 1s;
  background-color:#fff;
}
.contact1 {
  display: flex;
  justify-content: between;
  border-radius: 15px 50px 30px;
  height:450px;
