}
.contact2 img{
  height:450px;
  border-radius: 15px 50px 30px;
  padding-right: 50px;
}
.th{
  color:rgb(227, 187, 10);
}
.form{
  margin-left: -20px;
}
.footer{
  padding-top: 100px;
}

/* --------------GoToTop Button CSS----------- */
.top-btn{
  z-index:999;
  background-color:#fff ;
  border-radius: 50%;
  height:60px;
  width:60px;
  border : 4px solid rgb(219, 219, 219);
  position: sticky;
}
